import React, { useState, useEffect } from "react";
import { Button, Col, Row, List } from "antd";
import { Card, Image } from "antd";
import { FloatButton, Modal, Alert } from "antd";
import { Typography } from "antd";
// import './QuestionPage.module.css';
import Markdown from "./Markdown";

import { SingleChoice, MultipleChoice } from "./QuestionChoice";

import {
  QuestionCircleOutlined,
  LeftOutlined,
  RightOutlined,
} from "@ant-design/icons";

import QRScanner from "./QRScanner";

const { Text } = Typography;

function checkListsEqual(list1, list2) {
  if (list1.length !== list2.length) {
    return false;
  }
  
  const sortedList1 = list1.sort();
  const sortedList2 = list2.sort();
  
  for (let i = 0; i < sortedList1.length; i++) {
    if (sortedList1[i] !== sortedList2[i]) {
      return false;
    }
  }
  
  return true;
}


export default function QuestionPage({
  questions,
  setQuestions,
  setCurrentIndex,
  questionIndex,
  totalQuestions,
}) {
  const [showTip, setShowTip] = useState(false);
  const [showCard, setShowCard] = useState(false);
  const [image, setImage] = useState(null);

  // pc, mobile 
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  // const handleResize = () => {
  //   setWindowWidth(window.innerWidth);
  // };
  // window.addEventListener("resize", handleResize);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const {
    question,
    type,
    url,
    tip,
    answeredCorrectly,
    userAnswer,
  } = questions[questionIndex];
  console.log("question:", questions[questionIndex]);

  let { options, answer } = questions[questionIndex]
  if (type === "single_choice") {
    options = JSON.parse(options)
  } else if (type === "multi_choice") {
    options = JSON.parse(options)
    answer = JSON.parse(answer)
  }

  // 
  function handleShowTipClick() {
    setShowTip(!showTip);
  }

  function handlePreviousClick() {
    if (questionIndex === 0) return;
    setCurrentIndex(questionIndex - 1);
  }

  function handleNextClick() {
    if (questionIndex === totalQuestions - 1) return;
    setCurrentIndex(questionIndex + 1);
  }

  function handleCheckClick() {
    setShowCard(true);
  }

  function handleCheckAnswer(userAnswer, image) {
    console.log("handleCheckAnswer", userAnswer, image)
    if (image) setImage(image);

    let answeredCorrectly = false
    if (type === "multi_choice") {
      answeredCorrectly = checkListsEqual(answer, userAnswer)
    } else {
      answeredCorrectly = userAnswer === answer;
    }
    const newQuestions = [...questions];
    newQuestions[questionIndex] = {
      ...newQuestions[questionIndex],
      answeredCorrectly,
      // answeredWrongly: !answeredCorrectly,
      userAnswer: userAnswer,
    };
    // Update the questions array with the new data
    setQuestions(newQuestions);

    // 
    const answerCache = {};

    newQuestions.forEach(({ id, answeredCorrectly }) => {
      // console.log("answeredCorrectly=====", answeredCorrectly);
      if (answeredCorrectly !== undefined) {
        answerCache[id] = answeredCorrectly;
      }
    });
    localStorage.setItem("answerCache", JSON.stringify(answerCache));
  }

  function handleRestart() {
    setQuestions(
      questions.map(({ answeredCorrectly, userAnswer, ...rest }) => { return { ...rest, userAnswer: "" } })
    );

    localStorage.removeItem("answerCache")
  }

  const handleOk = () => {
    setShowTip(false);
  };
  const handleCancel = () => {
    setShowTip(false);
  };

  const handleCardOk = () => {
    setShowCard(false);
  };
  const handleCardCancel = () => {
    setShowCard(false);
  };

  function handleIndexClick(index) {
    console.log("click index", index);
    setCurrentIndex(index);
    setShowCard(false);
  }

  const renderItem = (item, index) => {
    console.log("item, index", index, typeof item.answeredCorrectly);

    let buttonStyle = {
      backgroundColor:
        item.answeredCorrectly === undefined
          ? "gray"
          : item.answeredCorrectly
          ? "green"
          : "red",
    };
    // if (index === ) {
    //   buttonStyle = {
    //     ...buttonStyle,
    //     outlineColor: "transparent",
    //     outlineStyle: "solid",
    //     boxShadow: "0 0 0 4px #5a01a7",
    //   };
    // }

    return (
      <List.Item>
        <Button style={buttonStyle} onClick={() => handleIndexClick(index)}>
          {index + 1}
        </Button>
      </List.Item>
    );
  };

  console.log(
    "question:",
    questionIndex,
    answeredCorrectly,
    typeof answeredCorrectly,
    answeredCorrectly === false
  );



  return (
    <div style={{ position: "relative", minHeight: "80vh" }}>
      {windowWidth > 768 ? (
        <Row gutter={16}>
          <Col span={12}>
            <Card
              title=""
              extra={
                <Text>
                  {questionIndex + 1}/{totalQuestions}
                </Text>
              }
            >
              <Markdown doc={question}></Markdown>
              {url && <Image src={url} />}
            </Card>
          </Col>

          <Col span={12}>
            <Card title="">
                <>
                  {type === "single_choice" && (
                    <SingleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e.target.value)}
                    />
                  )}

                  {type === "multi_choice" && (
                    <MultipleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e)}
                    />
                  )}

                  {type === "scan" && (
                    <QRScanner
                      key={questionIndex}
                      onAnswer={handleCheckAnswer}
                    />
                  )}

                  <Row justify="center">
                    {answeredCorrectly === true &&
                      <Alert message="" type="success" />
                    }
                    {answeredCorrectly === false &&
                      <Alert message=", " type="error" />
                    }
                    {image && <Image src={image} style={{ width: "100%" }} />}
                  </Row>
                </>
            </Card>
          </Col>
        </Row>
      ) : (
        <>
          <div style={{ width: "100%", height: "80vh", overflow: "auto" }}>
            <Card
              title=""
              extra={
                <Text>
                  {questionIndex + 1}/{totalQuestions}
                </Text>
              }
            >
              <Markdown doc={question}></Markdown>
              {url && <Image src={url} />}
            </Card>

            <Card title="">

                <>
                  {type === "single_choice" && (
                    <SingleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e.target.value)}
                    />
                  )}

                  {type === "multi_choice" && (
                    <MultipleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e)}
                    />
                  )}

                  {type === "scan" && (
                    <QRScanner
                      key={questionIndex}
                      onAnswer={handleCheckAnswer}
                    />
                  )}

                  <Row justify="center" gutter={[0, 16]}>
                    {answeredCorrectly === true &&
                      <Alert message="" type="success" />
                    }
                    {answeredCorrectly === false &&
                      <Alert message=", " type="error" />
                    }
                    {image && <Image src={image} style={{ width: "100%" }} />}
                  </Row>
                </>
            </Card>
          </div>

          {/* <input
            type="text"
            onChange={handleUserAnswerChange}
            value={userAnswer}
          /> */}
        </>
      )}

      <Row
        justify="center"
        align="middle"
        gutter={[16, 16]}
        style={{
          position: "fixed",
          zIndex: 9999,
          bottom: 0,
          left: 0,
          right: 0,
          height: "50px",
          backgroundColor: "white",
        }}
      >
        <Button onClick={handlePreviousClick} style={{ marginRight: "10px" }}>
          <LeftOutlined />
        </Button>
        <Button onClick={handleCheckClick} style={{ margin: "0 10px" }}>
          
        </Button>
        <Button onClick={handleNextClick} style={{ marginLeft: "10px" }}>
          <RightOutlined />
        </Button>
      </Row>

      <FloatButton.Group shape="circle" style={{ right: 24 }}>
        <FloatButton
          icon={<QuestionCircleOutlined />}
          onClick={handleShowTipClick}
        />
        {/* <FloatButton /> */}
        {/* <FloatButton.BackTop visibilityHeight={0} /> */}
      </FloatButton.Group>

      <Modal
        title=""
        open={showTip}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <p>{tip}</p>
      </Modal>

      <Modal
        title=""
        open={showCard}
        onOk={handleCardOk}
        onCancel={handleCardCancel}
      >
        <List
          grid={{ gutter: 16, column: 5 }}
          dataSource={questions}
          renderItem={renderItem}
          justify="start"
        ></List>
        <Row justify="center">
          <Button type="primary" onClick={handleRestart}>  </Button>
          </Row>
      </Modal>
    </div>
  );
}
