export default function Questionnaire() {
  // const [currentIndex, setCurrentIndex] = useState(0);
  const [questions, setQuestions] = useState([]);
  const [cardIndex, setCardIndex] = useState(0);

  useEffect(() => {
    get_questions()
      .then((data) => {
        const answerCacheStr = localStorage.getItem("answerCache");
        const answerCache = answerCacheStr ? JSON.parse(answerCacheStr) : {};

        data.forEach(item => {
          const { id, userAnswer } = item;
          if (id in answerCache) {
            Object.assign(item, { answeredCorrectly: answerCache[id] });
          }
        })

        setQuestions(data)
      })
      .catch((error) => console.error('Error fetching data:', error));
  }, []);

  // 
  let score = 0;
  questions.forEach((question) => {
    if (question.answeredCorrectly) {
      score += 1;
    }
  });
  localStorage.setItem("score", score);
  
  return (
    <>
      { questions.length > 0 && <QuestionPage
        key={cardIndex}
        questions={questions}
        setQuestions={setQuestions}
        setCurrentIndex={setCardIndex}
        questionIndex={cardIndex}
        totalQuestions={questions.length}
      />}
    </>
  );
}
